"use client"

import { useEffect, useState } from 'react'

interface AdSpaceProps {
  position: 'left' | 'right'
  width?: number
  height?: number
}

export function AdSpace({ position, width = 160, height = 600 }: AdSpaceProps) {
  const [mounted, setMounted] = useState(false)

  useEffect(() => {
    setMounted(true)
    // Here you can initialize ad networks like Google AdSense
    // Example: if (window.adsbygoogle) window.adsbygoogle.push({})
  }, [])

  if (!mounted) return null

  return (
    <aside 
      className={`hidden lg:block fixed ${position === 'left' ? 'left-0 lg:left-4' : 'right-0 lg:right-4'} top-1/2 -translate-y-1/2 z-[100] pointer-events-auto`}
      style={{
        width: `${width}px`,
        height: `${height}px`,
      }}
    >
        <div className="bg-white/80 backdrop-blur-md border border-gray-200 rounded-lg shadow-lg overflow-hidden hover:shadow-xl transition-shadow duration-200">
          <div className="bg-gradient-to-r from-blue-50 to-indigo-50 px-3 py-2 text-xs text-gray-500 text-center border-b border-gray-200">
            Sponsored
          </div>
          <div className="p-2">
            {/* Ad content placeholder */}
            <div className="bg-gray-50/90 backdrop-blur-sm rounded-md w-full h-[540px] flex flex-col items-center justify-center gap-3 border border-gray-200/50">
              <div className="text-sm text-gray-600 font-medium">Ad Space Available</div>
              <div className="text-xs text-gray-500 max-w-[140px] text-center">
                Advertise your travel business here
              </div>
            </div>
          </div>
          {/* Optional: Add close button or other controls */}
          <div className="px-2 py-1 text-[10px] text-gray-400 text-center border-t border-gray-100">
            Powered by BURNMILES
          </div>
        </div>
    </aside>
  )
}
